<template>
  <div class="variate-compare-chart-container">
    <div id="variate-compare-chart" v-show="showChart"></div>
    <div v-show="showChart" class="data-source">
      <span>蜂鸟</span>
    </div>
    <span class="nodata el-table__empty-block" v-if="!showChart">暂无数据</span>
  </div>
</template>

<script>
import dayjs from 'dayjs';
import {queryVarAutoSplit} from '@/api/analysis.js';
import {getColorObj} from '@/assets/scripts/getSomeSetting.js';
import {queryDecision} from '@/api/decision.js';

const allColorObj = getColorObj();
const colorArr = ['变动前三个月占比', '变动后三个月占比', '变动前三个月逾期率', '变动后三个月逾期率'].map(
  key => allColorObj[key]
);
export default {
  name: 'variate-compare-chart',
  data() {
    return {
      showChart: false
    };
  },
  mounted() {
    this.$on('drawVariateCompareChart', (initSample, theVar, updateTime) => {
      this.initSample = initSample;
      this.theVar = theVar;
      this.updateTime = updateTime;
      this.drawFunc();
    });
  },
  methods: {
    drawFunc() {
      this.showChart = true;
      this.getData()
        .then(result => {
          const data = this.dealWithData(result);
          if (Array.isArray(data) && data.length > 0) {
            if (!this.chart) {
              const elem = this.$el.querySelector('#variate-compare-chart');
              this.chart = echarts.init(elem);
            }
            const option = this.getOption(data);
            this.chart.setOption(option);
          } else {
            this.showChart = false;
          }
        })
        .catch(err => {
          console.log(err);
          this.showChart = false;
        });
    },
    getData() {
      return this.setVar().then(result => {
        this.theVar = result;
        return this.getT10Data(result);
      });
    },
    setVar() {
      return new Promise((resolve, reject) => {
        if (this.theVar.fieldtype === 'float') {
          queryVarAutoSplit(this.initSample[2], this.theVar.value)
            .then(result => {
              if (result.result && Array.isArray(result.result.variableAreaRangeList)) {
                this.theVar.rangeList = result.result.variableAreaRangeList;
                this.theVar.statisticType = 'SMART_RANGE';
              }
              resolve(this.theVar);
            })
            .catch(err => {
              console.log(err);
              reject(this.theVar);
            });
        } else {
          resolve(this.theVar);
        }
      });
    },
    getT10Data(item) {
      const data = {
        indexName: [this.initSample[2]],
        rcProductName: this.initSample[0],
        variablekey: item.value,
        createTimeStart: dayjs(this.updateTime)
          .add(-3, 'month')
          .format('YYYY-MM-DD'),
        createTimeEnd: dayjs(this.updateTime)
          .add(-1, 'day')
          .format('YYYY-MM-DD'),
        statisticConditionList: [
          {
            fieldName: item.value,
            statisticType: item.statisticType || 'FIELD_GROUP',
            rangeList: item.rangeList || []
          }
        ]
      };
      // 占比计数百分比
      let p1 = this.getStatistic('BEFORE_LOAN', data);
      // 变动后占比
      let p2 = this.getStatistic(
        'BEFORE_LOAN',
        Object.assign({}, data, {
          createTimeStart: this.updateTime,
          createTimeEnd: dayjs(this.updateTime)
            .add(3, 'month')
            .format('YYYY-MM-DD')
        })
      );
      // T5逾期率
      let p3 = this.getStatistic('AFTER_LOAN', data);
      // 变动后T5逾期率
      let p4 = this.getStatistic(
        'AFTER_LOAN',
        Object.assign({}, data, {
          createTimeStart: this.updateTime,
          createTimeEnd: dayjs(this.updateTime)
            .add(3, 'month')
            .format('YYYY-MM-DD')
        })
      );
      return Promise.all([p1, p2, p3, p4]);
    },
    getStatistic(type, data) {
      let reqData = Object.assign({}, data, {
        resultValueType: 'COUNT',
        filterConditionList: [],
        resultField: '',
        resultNeedPercent: true
      });
      if (type === 'BEFORE_LOAN') {
        Object.assign(reqData, {
          loanType: 'BEFORE_LOAN'
        });
      } else if (type === 'AFTER_LOAN') {
        Object.assign(reqData, {
          afterLoanKPI: 'T10',
          overdueDays: 'null',
          loanType: 'AFTER_LOAN'
        });
      }
      return queryDecision(reqData);
    },
    dealWithData(results) {
      let beforeData = [];
      let afterData = [];
      let beforeData1 = [];
      let afterData1 = [];
      if (
        results[0].result &&
        Array.isArray(results[0].result.originalReport) &&
        results[0].result.originalReport.length
      ) {
        beforeData = results[0].result.originalReport;
      }
      if (
        results[1].result &&
        Array.isArray(results[1].result.originalReport) &&
        results[1].result.originalReport.length
      ) {
        beforeData1 = results[1].result.originalReport;
      }
      if (
        results[2].result &&
        Array.isArray(results[2].result.originalReport) &&
        results[2].result.originalReport.length
      ) {
        afterData = results[2].result.originalReport;
      }
      if (
        results[3].result &&
        Array.isArray(results[3].result.originalReport) &&
        results[3].result.originalReport.length
      ) {
        afterData1 = results[3].result.originalReport;
      }
      const arr = beforeData.map(val => {
        let theOne = beforeData1.find(one => one.key === val.key);
        let theOne1 = afterData.find(one => one.key === val.key);
        let theOne2 = afterData1.find(one => one.key === val.key);
        let t5V, t5V1, t5V2;
        if (theOne) {
          t5V = theOne.value != null ? parseFloat(theOne.value) : undefined;
          if (typeof t5V === 'number') {
            t5V = Number.isInteger(t5V) ? t5V : t5V.toFixed(2);
          }
        }
        if (theOne1) {
          t5V1 = theOne1.value != null ? parseFloat(theOne1.value) : undefined;
          if (typeof t5V1 === 'number') {
            t5V1 = Number.isInteger(t5V1) ? t5V1 : t5V1.toFixed(2);
          }
        }
        if (theOne2) {
          t5V2 = theOne2.value != null ? parseFloat(theOne2.value) : undefined;
          if (typeof t5V2 === 'number') {
            t5V2 = Number.isInteger(t5V2) ? t5V2 : t5V2.toFixed(2);
          }
        }
        return {
          key: val.key,
          v1: parseFloat(val.value),
          v2: t5V,
          v3: t5V1,
          v4: t5V2
        };
      });
      return arr;
    },
    getOption(data = []) {
      // 整理获得需要的数据
      let option = {
        title: {
          top: 0,
          text: this.theVar.label + '分布图',
          left: 'center',
          textStyle: {
            fontWeight: 'bold',
            fontSize: 16
          }
        },
        color: colorArr,
        legend: {
          data: ['变动前三个月占比', '变动后三个月占比', '变动前三个月逾期率', '变动后三个月逾期率'],
          top: 40,
          left: 60
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'shadow'
          },
          formatter: params => {
            const arr = params.map(item => {
              return `${item.marker} ${item.seriesName}：${item.data || 0}%`;
            });
            arr.unshift(params[0].name);
            return arr.join('<br />');
          },
          padding: [5, 12]
        },
        grid: {
          top: '30%',
          left: 60,
          right: 70,
          bottom: '20%',
          containLabel: true
        },
        xAxis: [
          {
            type: 'category',
            name: '变量取值',
            nameGap: 30,
            nameLocation: 'center',
            scale: true,
            boundaryGap: true,
            axisLine: {
              onZero: false
            },
            splitLine: {
              show: false
            },
            // splitNumber: 20,
            data: data.map(v => v.key)
          }
        ],
        yAxis: [
          {
            name: '占比（%）',
            scale: true,
            splitArea: {
              show: true
            },
            axisLine: {
              lineStyle: {
                color: colorArr[0]
              }
            },
            min: 0,
            boundaryGap: [0.2, 0.2]
          },
          {
            name: '逾期率（%）',
            scale: true,
            splitArea: {
              show: true
            },
            axisLine: {
              lineStyle: {
                color: colorArr[1]
              }
            },
            min: 0,
            position: 'right',
            boundaryGap: [0.2, 0.2]
          }
        ],
        series: [
          {
            name: '变动前三个月占比',
            type: 'bar',
            yAxisIndex: 0,
            barGap: 0,
            data: data.map(v => v.v1)
          },
          {
            name: '变动后三个月占比',
            type: 'bar',
            yAxisIndex: 0,
            data: data.map(v => v.v2)
          },
          {
            name: '变动前三个月逾期率',
            type: 'line',
            yAxisIndex: 1,
            data: data.map(v => v.v3)
          },
          {
            name: '变动后三个月逾期率',
            type: 'line',
            yAxisIndex: 1,
            data: data.map(v => v.v4)
          }
        ]
      };
      if (option.xAxis[0].data.length > 30) {
        option.dataZoom = [
          {
            type: 'slider',
            y: '90%',
            start: 0,
            end: 100
          }
        ];
      }
      return option;
    }
  }
};
</script>

<style scoped>
#variate-compare-chart {
  position: relative;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  height: 360px;
}
</style>
