<template>
  <div class="variation-monitor-variate">
    <div class="variate-list-container">
      <ul>
        <li v-for="(item, i) in variateList" :key="i" :class="{'active': item.isActive}">
          <span @click.stop.prevent="changeVar(item)">{{item.label}}</span>
        </li>
      </ul>
    </div>
    <div class="chart-container">
      <univariate-monitor-chart ref="univariateMonitorChart" class="univariate-monitor-box"></univariate-monitor-chart>
      <variate-compare-chart ref="variateCompareChart" class="compare-chart-box"></variate-compare-chart>
    </div>
  </div>
</template>

<script>
import dayjs from 'dayjs';
import {getVarList} from '@/api/sample.js';
import variateCompareChart from '@/components/variate-compare-chart';
import univariateMonitorChart from '@/components/univariate-monitor-chart';

export default {
  name: 'variation-monitor-variate',
  components: {
    variateCompareChart,
    univariateMonitorChart
  },
  data() {
    return {
      variateList: []
    };
  },
  computed: {
    moduleName() {
      return this.$store.state.sample.moduleName;
    },
    productName() {
      return this.$store.state.sample.productName;
    },
    updateTime() {
      return this.$store.state.sample.updateTime;
    }
  },
  watch: {
    moduleName: {
      handler: function(newV) {
        if (newV) {
          this.$nextTick(() => {
            this.getVars();
          });
        }
      },
      immediate: true
    }
  },
  mounted() {},
  methods: {
    getVars() {
      getVarList(this.moduleName)
        .then(result => {
          if (Array.isArray(result.result)) {
            this.variateList = result.result.map(item => {
              item.label = `${item.vbname}${item.fieldtype === 'float' ? '（连续）' : '（离散）'}`;
              item.value = item.key;
              return item;
            });
            for (let item of this.variateList) {
              this.$set(item, 'isActive', false);
            }
            this.changeVar(this.variateList[0]);
            return;
          }
          this.variateList = [];
        })
        .catch(err => {
          console.log(err);
          this.variateList = [];
        });
    },
    changeVar(item) {
      console.log(item);
      this.variateList.forEach(item => (item.isActive = false));
      item.isActive = true;
      this.$nextTick(() => {
        this.$refs.variateCompareChart.$emit(
          'drawVariateCompareChart',
          [this.productName, 0, this.moduleName],
          item,
          this.updateTime
        );
        this.$refs.univariateMonitorChart.$emit(
          'drawUnivariateMonitorChart',
          this.moduleName,
          item.key,
          dayjs(this.updateTime)
            .add(-3, 'month')
            .format('YYYY-MM-DD'),
          dayjs(this.updateTime)
            .add(3, 'month')
            .format('YYYY-MM-DD')
        );
      });
    }
  }
};
</script>

<style scoped>
.variation-monitor-variate {
  width: 100%;
  display: flex;
}
.variate-list-container {
  width: 300px;
  height: 720px;
  overflow: auto;
}
li {
  display: flex;
  align-items: center;
  height: 36px;
  padding-left: 10px;
  box-sizing: border-box;
}
span {
  cursor: pointer;
}
.active {
  color: #409eff;
  background: #e8f5ff;
}
.chart-container {
  width: calc(100% - 300px);
}
.compare-chart-box,
.univariate-monitor-box {
  width: 100%;
  height: 360px;
  margin-bottom: 15px;
}
</style>
