<template>
  <div class="variation-monitor-refause">
    <el-table :data="rcrefuseList1" stripe :header-row-style="headerStyle">
      <el-table-column prop="variableName" label="命中规则名称" align="center"></el-table-column>
      <el-table-column prop="count" label="总数" width="120" align="center"></el-table-column>
      <el-table-column prop="refuserate" label="命中规则比率" width="120" align="center"></el-table-column>
    </el-table>
    <div class="compare-icon">
      <span>VS</span>
    </div>
    <el-table :data="rcrefuseList2" :header-row-style="headerStyle">
      <el-table-column prop="variableName" label="命中规则名称" align="center"></el-table-column>
      <el-table-column prop="count" label="总数" width="120" align="center"></el-table-column>
      <el-table-column prop="refuserate" label="命中规则比率" width="120" align="center"></el-table-column>
    </el-table>
  </div>
</template>

<script>
import dayjs from 'dayjs';
import {getRcrefuseList} from '@/api/monitor.js';

export default {
  name: 'refause-variation-monitor',
  data() {
    return {
      decisionDates1: [],
      decisionDates2: [],
      headerStyle: {
        color: '#409eff'
      },
      rcrefuseList1: [],
      rcrefuseList2: []
    };
  },
  computed: {
    moduleName() {
      return this.$store.state.sample.moduleName;
    },
    updateTime() {
      return this.$store.state.sample.updateTime;
    }
  },
  watch: {
    moduleName: {
      handler: function(newV) {
        if (newV) {
          this.$nextTick(() => {
            this.getVariationList();
          });
        }
      },
      immediate: true
    },
    updateTime: {
      handler: function(newV) {
        if (newV) {
          this.decisionDates1 = [
            dayjs(newV)
              .add(-3, 'month')
              .format('YYYY-MM-DD'),
            newV
          ];
          this.decisionDates2 = [
            newV,
            dayjs(newV)
              .add(3, 'month')
              .format('YYYY-MM-DD')
          ];
        }
      },
      immediate: true
    }
  },
  created() {},
  mounted() {},
  methods: {
    getVariationList() {
      this.getRcrefuseList1();
      this.getRcrefuseList2();
    },
    getRcrefuseList1() {
      getRcrefuseList(this.moduleName, this.decisionDates1[0], this.decisionDates1[1])
        .then(result => {
          if (Array.isArray(result.result) && result.result.length) {
            this.rcrefuseList1 = result.result.map(item => {
              let num = item.refuserate;
              if (typeof num === 'number') {
                num = Number.isInteger(num) ? num : num.toFixed(2);
              }
              item.refuserate = `${num}%`;
              return item;
            });
            return;
          }
          this.rcrefuseList1 = [];
        })
        .catch(err => {
          console.log(err);
          this.rcrefuseList1 = [];
        });
    },
    getRcrefuseList2() {
      getRcrefuseList(this.moduleName, this.decisionDates2[0], this.decisionDates2[1])
        .then(result => {
          if (Array.isArray(result.result) && result.result.length) {
            this.rcrefuseList2 = result.result.map(item => {
              let num = item.refuserate;
              if (typeof num === 'number') {
                num = Number.isInteger(num) ? num : num.toFixed(2);
              }
              item.refuserate = `${num}%`;
              return item;
            });
            return;
          }
          this.rcrefuseList2 = [];
        })
        .catch(err => {
          console.log(err);
          this.rcrefuseList2 = [];
        });
    }
  }
};
</script>

<style scoped>
.variation-monitor-refause {
  display: flex;
  align-items: start;
}
.compare-icon {
  width: 100px;
  display: flex;
  justify-content: center;
  margin-top: 50px;
  font-size: 36px;
}
</style>
